@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  font-size: 16px;
}

.h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.h2 span {
  display: inline-block;
  margin-left: 15px;
  font-size: 0.8em;
}

.h2 span em {
  font-size: 0.6em;
  color: #777;
  font-style: normal;
}

.close-x {
  stroke: black;
  fill: transparent;
  stroke-linecap: round;
  stroke-width: 5;
}

ol li {
  list-style-type: decimal;
}

.danger {
  color: red;
  font-size: 12px;
}

.container {
  display: flex;
  flex-direction: column;
  max-width: 700px;
  height: 100vh;
  margin: 0 auto;
}

.content {
  flex: 1 1 auto;
  overflow: auto;
  min-height: 0;
}

.input {
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-radius: 20px;
  border: 1px solid #afafaf;
  font-size: 20px;
}

.input:focus {
  border-color: #2c8af0;
}

.input:focus.has-error {
  box-shadow: 0 0 5px red;
}

.input.has-error {
  border-color: red;
}

.btn {
  height: 40px;
  display: inline-flex;
  padding: 0 30px;
  background: #2c8af0;
  color: #fff;
  border-radius: 20px;
  font-size: 18px;
  transition: background .3s;
}

.btn:hover {
  background: #147dee;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background: #2c8af0;
}

.form-start {
  margin-bottom: 20px;
}

.form-start__field {
  background: #f6f6f6;
  padding: 10px;
  display: flex;
}

.form-start__input {
  flex: 1 0 auto;
  margin-right: 10px;
}

.forms {
  background: #f6f6f6;
}

.form {
  padding: 10px;
}

.form-group {
  font-size: 0;
  margin: 0 -10px -10px;
}

.form-item {
  display: inline-block;
  margin: 0 10px 10px;
  font-size: 14px;
}

.btn-close {
  background: none;
  border: none;
  display: inline-block;
  width: 30px;
  height: 30px;
}

.checkbox {
  position: relative;
  cursor: pointer;
}

.checkbox input {
  position: absolute;
  opacity: 0;
  visibility: hidden;
}

.checkbox input:checked + span:after {
  background: #6e59b5;
}

.checkbox input:checked + span:before {
  opacity: 1;
}

.checkbox span {
  position: relative;
  padding-left: 30px;
}

.checkbox span:after {
  content: '';
  position: absolute;
  left: 0;
  top: -1px;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  box-shadow: inset 0 0 0 2px #6e59b5;
  background: #fff;
  z-index: 1;
}

.checkbox span:before {
  content: '';
  position: absolute;
  left: 5px;
  top: 5px;
  width: 10px;
  height: 6px;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
  transform: rotate(-50deg);
  z-index: 2;
  opacity: 0;
}

.select {
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.select:after {
  content: "▼";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  font-size: 60%;
  line-height: 30px;
  padding: 0 7px;
  color: #6e59b5;
  pointer-events: none;
}

.select select {
  display: inline-block;
  border: 0;
  border-bottom: 2px solid #6e59b5;
  padding: 4px 25px 4px 5px;
  margin: 0;
  height: 30px;
  background: none;
  -webkit-appearance: none;
  border-radius: 0;
  cursor: pointer;
  font-size: 14px;
}

.repo {
  display: block;
  padding: 10px 0;
  color: #000;
  text-decoration: none;
}

.repo__name {
  font-weight: 500;
  font-size: 20px;
  margin-bottom: 8px;
}

.repo__forked {
  font-size: 12px;
  color: #bebebe;
  margin-bottom: 10px;
}

.repo__description {
  margin-bottom: 8px;
}

.repo__footer {
  display: flex;
  font-size: 14px;
}

.repo__stars, .repo__lang {
  margin-right: 10px;
}

body.is-type-filter .repo__forked {
  display: none;
}

body.is-lang-filter .repo__lang {
  display: none;
}

.list {
  padding: 0 10px;
  margin-bottom: 20px;
}

.list__item {
  border-bottom: 1px solid #afafaf;
}

.pagination {
  padding: 10px;
  text-align: center;
}

.modal {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background: rgba(0, 0, 0, 0.4);
  z-index: 99;
}

.modal__inner {
  position: relative;
  max-width: 500px;
  width: 100%;
  background: #fff;
  border-radius: 5px;
  padding: 20px 30px;
}

.modal__title {
  font-size: 24px;
  text-align: center;
  margin-bottom: 10px;
  padding: 0 30px;
}

.modal__close {
  position: absolute;
  right: 20px;
  top: 20px;
}

.info:not(:last-child) {
  margin-bottom: 10px;
}

.info a {
  color: #6e59b5;
  text-decoration: none;
  transition: color .3s;
}

.info a:hover {
  color: #574497;
  text-decoration: underline;
}

.info ol li:not(:last-child) {
  margin-bottom: 10px;
}

.info__property {
  margin-bottom: 10px;
}

.info__value {
  padding-left: 20px;
}

.info__row {
  padding: 10px 0;
}

.info__row:after {
  content: " ";
  display: table;
  clear: both;
}

.info__cell {
  float: left;
  width: 50%;
}

.info__value ol {
  padding-left: 15px;
}

.info__name {
  position: relative;
  padding-left: 40px;
  text-decoration: none;
}

.info__img {
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -15px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: center center / cover no-repeat;
}
